import React, { Component } from 'react';

import BaseGrid from './base-grid';

import Button from 'muicss/lib/react/button';
import Input from 'muicss/lib/react/input';
import Row from 'muicss/lib/react/row';
import Col from 'muicss/lib/react/col';

import appstore from './images/appstore.svg';
import googleplay from './images/google-play-store-badge-2.png';

import './landing-section.css';


class LandingSection extends Component {
  render() {
    let firstColumn = (
      <div className="content-text">
        <p className="content-text-title">{this.props.title}</p>
        <p className="content-text-subtitle">{this.props.text}</p>
      </div>
    );
    let secondColumn = (
      <img src={this.props.imageSrc} 
           alt={this.props.imageAlt} 
           className="img-responsive center-block" />
    );
    if (this.props.sectionId === 0 || this.props.sectionId + 1 === this.props.numberOfSections) {
      let googleBadge = <a href=''><img alt='Get it on Google Play' className="download-badge" src={googleplay}/></a>;
      let appStoreBadge = <a href=''><img alt='Get it on AppStore' className="download-badge" src={appstore}/></a>;
      firstColumn = (
        <div>
          <Row>
            <Col sm="12">
              {firstColumn}
            </Col>
          </Row>
          <Row className="row-margin-bottom">
            <Col sm="12">
              <div className="mui--pull-left">
                {googleBadge}
              </div>
              <div className="mui--pull-right">
                {appStoreBadge}
              </div>
              <div className="mui--clear-fix" />
            </Col>
          </Row>
        </div>
      );
      if (this.props.sectionId === 0) {
        secondColumn = (
          <div>
            <Input placeholder="Email" type="email" color="primary"></Input>
            <Input placeholder="Password" type="password"></Input>
            <Button variant="raised" color="primary">Sign Up</Button>
          </div>
        );
      }
    }
    
    let sectionStyles = {
      backgroundColor: this.props.color
    };
    return (
      <div className="landing-section" style={sectionStyles} id={this.props.id}>
        <BaseGrid firstColumn={firstColumn}
                  secondColumn={secondColumn} />
      </div>
    );
  }
}

export default LandingSection;


