import React, { Component } from 'react';

import './vertical-nav-stepper.css';

import mobilePng from './images/nav-mobile.png';
import partnerPng from './images/nav-partner.png';
import voiceActingPng from './images/nav-voice-acting.png';
import learnWithFunPng from './images/nav-learn-with-fun.png';
import languagePng from './images/nav-loyts.png';
import userPng from './images/user.png';

class VerticalNavigationStepper extends Component {
  constructor(props) {
    super(props);
    this.state = {
      hover: undefined,
    };
  }

  isHover(i) {
    return !this.isActive(i) && i === this.state.hover;
  }

  isActive(i) {
    return i === this.props.item;
  }

  onMouseEnter(i) {
    this.setState({hover: i});
  }

  onMouseLeave() {
    this.setState({hover: undefined});
  }

  render() {
    let navigationItems = [
      {
        imgSrc: userPng,
        text: "Sign Up",
      },
      {
        imgSrc: languagePng,
        text: "Choose a Language",
      },
      {
        imgSrc: partnerPng,
        text: "Choose a partner",
      },
      {
        imgSrc: learnWithFunPng,
        text: "Learn with fun",
      },
      {
        imgSrc: voiceActingPng,
        text: "Improve your speaking skills",
      },
      {
        imgSrc: mobilePng,
        text: "Install an application"
      }
    ];
    let self = this;
    let navigationItemClassNames = function(i) {
      if (self.isActive(i)) {
        return "navigation-item navigation-item-active";
      } else if (self.isHover(i)) {
        return "navigation-item navigation-item-hover";
      } else {
        return "navigation-item";
      }
    };

    let navigation = navigationItems.map((item, i) => 
      <div className={navigationItemClassNames(i)}
           key={i}
           onMouseEnter={() => this.onMouseEnter(i)}
           onMouseLeave={() => this.onMouseLeave()}>
        <a className="navigation-link"
           onClick={() => this.props.onClick(i)}>
          <img src={item.imgSrc} alt={item.text} className="navigation-link-img"/>
          <div className="navigation-link-text" style={{display: (this.isActive(i) || this.isHover(i)? "block" : "none")}}>
            {item.text}
          </div>
        </a>
        <div className={i + 1 === navigationItems.length? "" : "navigation-link-border"} />
      </div>
    );

    return (
      <div className="vertical-navigation-stepper-bar hidden-xs hidden-sm">
        {navigation}
      </div>
    );
  }
}

export default VerticalNavigationStepper;


