import React, { Component } from 'react';
import './App.css';
import { scroller } from 'react-scroll';

import tree from './images/1-tree-new.png';
import world from './images/2-map_2.png';
import youtube from './images/4-voiceacting.png';
import phone from './images/phone.png';
import words from './images/words.png';

import LinkSection from './link-section';
import VerticalNavigationStepper from './vertical-nav-stepper';
import LandingSection from './landing-section';


class App extends Component {
  constructor(props) {
    super(props);
    this.onKeyDown = this.onKeyDown.bind(this);
    this.onKeyUp = this.onKeyUp.bind(this);
    this.onScroll = this.onScroll.bind(this);
    this.lastEventTime = -1;
    this.state = {
      slideId: 0,
      numberOfSlides: 6,
      sectionIds: ["sign-up", "welcome", "connect-peoples", "games", "voice-acting", "install"],
    };
  }

  scrollTo(scrollTo) {
    scroller.scrollTo(scrollTo, {
      duration: 800,
      smooth: true,
      ignoreCancelEvents: true
    });
  }

  prevSlide() {
    if (this.state.slideId > 0) {
      let curSlide = this.state.slideId;
      this.setState({slideId: curSlide - 1});
      this.scrollTo(this.state.sectionIds[curSlide - 1]);
    }
  }

  toSlide(i) {
    if (0 <= i && i < this.state.numberOfSlides) {
      this.setState({slideId: i});
      this.scrollTo(this.state.sectionIds[i]);
    }
  }

  nextSlide() {
    if (this.state.slideId + 1 < this.state.numberOfSlides) {
      let curSlide = this.state.slideId;
      this.setState({slideId: curSlide + 1});
      this.scrollTo(this.state.sectionIds[curSlide + 1]);
    }
  }

  canProcessEvent() {
    const oneSecond = 1000;
    let curTime = Date.now();
    return curTime - this.lastEventTime > oneSecond;
  }

  mouseEvent(event) {
    event.preventDefault();
    let deltaY = event.deltaY;
    if (this.canProcessEvent()) {
      this.lastEventTime = Date.now();
      if (deltaY < 0) {
        this.prevSlide();
      } else {
        this.nextSlide();
      }
    }
  }

  onKeyDown(event) {
    if (event.keyCode === 40 && this.canProcessEvent()) {
      this.lastEventTime = Date.now();
      this.nextSlide();
    }
  }

  onKeyUp(event) {
    if (event.keyCode === 38 && this.canProcessEvent()) {
      this.lastEventTime = Date.now();
      this.prevSlide();
    }
  }

  onScroll(event) {
    event.preventDefault();
    if (this.canProcessEvent()) {
      this.lastEventTime = Date.now();
      if (event.detail < 0) {
        this.prevSlide();
      } else {
        this.nextSlide();
      }
    }
  }

  render() {
    let sections = [
      {
        title: "LOYTS",
        text: "The best app to practice a language. Create an account now. It's free",
        nextSlideLinkText: "",
        imageSrc: "",
        imageAlt: "",
        color: "#262626",
      },
      {
        title: "Choose Language",
        text: "With LOYTS you can practice every language you want. Which apple do you like?",
        nextSlideLinkText: "How it works",
        imageSrc: tree,
        imageAlt: "tree",
        color: "#60bdfc",
      },
      {
        title: "Connect With People",
        text: "Record a short audio to draw peoples' attention. Listen their applications and choose the one you like",
        nextSlideLinkText: "Learn with fun",
        imageSrc: world,
        imageAlt: "world",
        color: "#fc9760",
      }, 
      {
        title: "Fascinating Games",
        text: "To make your practicing exciting play in games like Two-Truth-And-a-Lie or Would-You-Rather. With communicative games you will practice language with fun",
        nextSlideLinkText: "Train pronunciation",
        imageSrc: words,
        imageAlt: "games",
        color: "#76C961",
      },
      { 
        title: "Breathtaking Voice Acting",
        text: "Make a voice over for a video from YouTube. Compare your prononication with original one. Try again if you are not satisfied. It helps to make your pronunciation clear",
        nextSlideLinkText: "Install app on smartphone",
        imageSrc: youtube,
        imageAlt: "man speaks on youtube",
        color: "#7C7AD2",
      }, 
      {
        title: "Install Android or iOS app",
        text: "Stay with LOYTS everywhere. Install an app on your device (Android, iOS) and practice language you want wherever you want",
        nextSlideLinkText: "",
        imageSrc: phone,
        imageAlt: "mobile",
        color: "#DD6558",
      }
    ];
    sections.forEach((item, i) => item.id = this.state.sectionIds[i]);
    let sections_tags = sections.map((section, id) => 
      <LandingSection scrollTo={id + 1 < sections.length? sections[id + 1].id : undefined} 
                      title={section.title} 
                      text={section.text}
                      imageSrc={section.imageSrc}
                      sectionId={id}
                      numberOfSections={this.state.numberOfSlides}
                      color={sections[this.state.slideId].color}
                      id={section.id}
                      key={section.id}>
      </LandingSection>
    );
    let navigationBar = (
      <VerticalNavigationStepper item={this.state.slideId}
                                 left={this.state.navLeft}
                                 top={this.state.navTop}
                                 onClick={(i) => this.toSlide(i)}/>
    );
    if (this.state.slideId === 0) {
      navigationBar = (<div/>);
    }
    return (
      <div className="sections" 
           onWheel={(e) => this.mouseEvent(e)}
           tabIndex={0}
           onKeyDown={(e) => this.onKeyDown(e)}
           onKeyUp={(e) => this.onKeyUp(e)}
           onScroll={(e) => this.onScroll(e)}>
        {sections_tags}
        {navigationBar}
        {this.state.slideId + 1 === this.state.numberOfSlides? 
          (<div />)
          : 
          (<LinkSection onClick={() => this.nextSlide()}
                         nextSlideLinkText="Discover"/>)
        }
      </div>
    );
  }
}

export default App;



