import React, { Component } from 'react';
import Container from 'muicss/lib/react/container';
import Row from 'muicss/lib/react/row';
import Col from 'muicss/lib/react/col';
import './base-grid.css';

class BaseGrid extends Component {
  render() {
    return (
      <Container>
        <Row className="row-margin-top">
          <Col xs-offset="1" xs="10" sm-offset="1" sm="10" md-offset="2" md="4">
            {this.props.firstColumn}
          </Col>
          <Col xs-offset="1" xs="10" sm-offset="1" sm="10" md-offset="0" md="6">
            {this.props.secondColumn}
          </Col>
        </Row>
      </Container>
    );
  }
}

export default BaseGrid;



